import React from "react"

import Layout from "../../components/Layout"

import { Link } from "gatsby"

import img1 from "../../images/territorio/score1.png";
import img2 from "../../images/territorio/story1.png";
import img3 from "../../images/territorio/who2.png";

export default function Home() {
  return (
    <Layout breadcrumbs={["Território em desenvolvimento"]}>
      <div id="heading" >
        <h1>Território em desenvolvimento</h1>
      </div>

      {/* Main */}
      <section id="main" className="wrapper">
        <div className="inner">

          <div className="content page">

            {<h5><Link to="/">Inicio</Link> &gt;&gt; Projetos  &gt;&gt; Origens Brasil<span className="registered">®</span></h5>}

            {/* <header>
              <h2>Garantia de negócios pela floresta em pé</h2>
            </header> */}            

            <div style={{ display: 'flex' }}>
              <div>
                <p className="headline">Indicadores nos ajudam não apenas a melhor entender o mundo, mas também a transformá-lo!</p>
                <p>O IPS é uma forma inovadora de mensurar a qualidade de vida e o bem-estar de territórios – sejam bairros, cidades, regiões ou países – que se adapta às realidades locais, empoderando os gestores públicos e a sociedade civil para melhor estabelecer metas de desenvolvimento. </p>
                <p>A aplicação do IPS no interior do Paraná é resultado de uma parceria entre a Instituição Klabin, a Fundação Avina, a Interação Urbana e Diferencial e o Instituto Agir Ambiental. O Engajados é responsável pela concepção e desenvolvimento da plataforma.</p>
                <p>Nosso site, em construção, oferecerá ao público interessado uma maneira mais didática de interagir com os dados, de se apropriar dos mesmos e de colaborar pelo bem da cidade. Gestores, servidores públicos, sociedade civil organizada, educadores, empresários e comunidades terão a sua disposição um espaço de informação e articulação territorial.  </p>
                <p>Na primeira fase do projeto, o Instituto Agir Ambiental moderará a articulação de cinco grupos de trabalho dentro da plataforma sobre os temas: água e saneamento, igualdade de gênero, segurança alimentar, coleta seletiva, e acesso à educação básica.</p>

                <ul>
                  <li>Acesso ao projeto em: <a href="http://territorio.app.br/" target="_blank">territorio.app.br</a></li>
                </ul>
              </div>
              <div>
                <span className="image special"><img src={img1} alt="" /></span>
                <span className="image special"><img src={img2} alt="" /></span>
                <span className="image special"><img src={img3} alt="" /></span>
              </div>
            </div>

          </div>
        </div>
      </section>
    </Layout>
  )
}
